* {
    margin: 0;
    padding: 0;

}

body {
    height: 100vh;
    width: 100%;
    overflow-x: hidden;
}

#root {
    height: auto;
    width: 100%;
}

.background {
    background-color: #3330E4;
}

.footer {
    position: fixed;
    bottom: 2px;
    left: 0;
    width: 100%;
}

.formy {
    height: 250px;
    width: 80%;
    margin: auto;
    margin-top: 1rem;
}

.cardttl {
    height: 7rem;
}

.myspinner {

    margin: auto;
    margin-top: 10px;
}

.spinnercol {
    background: rgb(147, 125, 194);
    background: linear-gradient(319deg, rgba(147, 125, 194, 1) 25%, rgba(200, 129, 200, 1) 50%, rgba(252, 197, 192, 1) 100%);
    height: 3.5rem;
    width: 3.5rem;
}

.modes {
    display: flex;
    justify-content: space-evenly;
    height: 50px;
    margin-top: 1rem;
    font-size: 1.28rem;
    font-weight: 700;
    color: #D61C4E;
}


.lightbg {
    background-color: #2C3333;
    border: none;
    color: white;
}

.lightbg:hover {
    background-color: #2C3333;
    color: white;
}